import axios from 'axios'
import { useSnackbar } from 'notistack'
import { useAuthHeader, useAuthUser, useSignOut } from 'react-auth-kit'

import { Box, Button, Dialog, Typography } from '@mui/material'

interface WithdrawModalProps {
  open: boolean
  handleClose(): void
}

const WithdrawModal: React.FC<WithdrawModalProps> = ({ open, handleClose }) => {
  const authUser = useAuthUser()
  const authHeader = useAuthHeader()
  const signOut = useSignOut()

  const { enqueueSnackbar } = useSnackbar()

  const handleSubmit = () => {
    axios
      .delete(`${process.env.REACT_APP_APIHOST}/user?userId=${authUser()?.id}`, {
        headers: { Authorization: authHeader() }
      })
      .then(({ data }) => {
        if (data.message !== 'ok') throw new Error()

        enqueueSnackbar('  .  !', { variant: 'success' })
        signOut()
      })
      .catch(err => {
        enqueueSnackbar(err.response?.data?.message ?? '    .  ', {
          variant: 'error'
        })
      })
  }

  return (
    <Dialog open={open} onClose={handleClose} maxWidth="xs">
      <Box p={3}>
        <Typography color="error" fontWeight="bold">
             ,          .
        </Typography>
        <Typography fontWeight="bold">
             ,     (     )
          .
        </Typography>
        <br />
        <Typography variant="h4"></Typography>
        <Typography>1.    ,     7   .</Typography>
        <Typography>
          2.        ,       
           .
        </Typography>

        <Box display="flex" justifyContent="space-evenly" mt={2}>
          <Button variant="outlined" onClick={handleClose} sx={{ paddingX: 4 }}>
            
          </Button>
          <Button variant="contained" color="error" onClick={handleSubmit} sx={{ paddingX: 4 }}>
            
          </Button>
        </Box>
      </Box>
    </Dialog>
  )
}

export default WithdrawModal
